.notFoundPageWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    text-align: center;
}
.link_div {
    display: inline-block;
    cursor: pointer;
    border: 2px green solid;
    background-color: rgb(156, 160, 157);
    border-radius: 10px;
    padding: 10px;
}
.link {
    text-decoration: none;
    color: green;
    font-size: 25px;
    font-weight: bold;
}